@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.content {
  width: 88%;
  margin-left: 12%;
}


.dashboard {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: space-around;
  font-size: 3rem;
  background-color: #0069B6;
}

.analytics {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  background-color: #0069B6;
}

.plan {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  background-color: #0069B6;
}



.nav-menu {
  background-color: #005A9C;
  width: 12%;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
  min-width: 175px;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
  font-family: 'Roboto';
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #1a83ff;
}

.nav-menu-items {
  width: 100%;
}

span {
  margin-left: 16px;
}

